import * as React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"

import Layout from "../components/layout"
import Seo from "../components/seo"

const IndexPage = () => (
  <Layout>
    <Seo title="Home" />
    <main className="flex-shrink-0">
      <header className="bg-dark py-5">
        <div className="container px-5">
          <div className="row gx-5 align-items-center justify-content-center">
            <div className="col-lg-8 col-xl-7 col-xxl-6">
              <div className="my-5 text-center text-xl-start">
                <h1 className="display-5 fw-bolder text-white mb-2">
                  モダン組み込みソフト開発
                </h1>
                <p className="lead fw-normal text-white-50 mb-4">
                  世界に通用する日本のものづくりへ。
                </p>
              </div>
            </div>
            <div className="col-xl-5 col-xxl-6 d-none d-xl-block text-center">
              <div
                id="carouselExampleInterval"
                className="carousel slide"
                data-bs-ride="carousel"
              >
                <div className="carousel-inner">
                  <div className="carousel-item active" data-bs-interval={5000}>
                    <img
                      src="/images/logo-embedded-wizard.svg"
                      className="d-block w-100"
                      alt="..."
                    />
                  </div>
                  <div className="carousel-item" data-bs-interval={5000}>
                    <img
                      src="/images/logo-safertos.svg"
                      className="d-block w-100 bg-light"
                      alt="..."
                    />
                  </div>
                  <div className="carousel-item" data-bs-interval={5000}>
                    <img
                      src="/images/logo-tracealyzer.svg"
                      className="d-block w-100 bg-light"
                      alt="..."
                    />
                  </div>
                  <div className="carousel-item" data-bs-interval={5000}>
                    <img
                      src="/images/logo-cyclone-tcp.svg"
                      className="d-block w-100 bg-light"
                      alt="..."
                    />
                  </div>
                </div>
                <button
                  className="carousel-control-prev"
                  type="button"
                  data-bs-target="#carouselExampleInterval"
                  data-bs-slide="prev"
                >
                  <span
                    className="carousel-control-prev-icon"
                    aria-hidden="true"
                  />
                  <span className="visually-hidden">Previous</span>
                </button>
                <button
                  className="carousel-control-next"
                  type="button"
                  data-bs-target="#carouselExampleInterval"
                  data-bs-slide="next"
                >
                  <span
                    className="carousel-control-next-icon"
                    aria-hidden="true"
                  />
                  <span className="visually-hidden">Next</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </header>
      {/* Embedded Wizard */}
      <section class="py-5 bg-light" id="scroll-target">
        <div class="container px-5 my-5">
          <div class="row gx-5 align-items-center">
            <div class="col-lg-6">
              <img
                class="img-fluid rounded mb-5 mb-lg-0"
                src="/images/logo-embedded-wizard.svg"
                alt="..."
              />
            </div>
            <div class="col-lg-6">
              <h2 class="fw-bolder">魔法のようなUI開発</h2>
              <p class="lead fw-normal text-muted mb-0">
                ドイツTara Systems社が開発した組み込み専用GUI開発ツール。
                <br />
                高品質で迅速な UI開発をサポートします。
              </p>
              {/* <div className="my-4">
                <a
                  href="/product_embedded_wizard/"
                  class="btn btn-primary btn-lg"
                  aria-disabled="true"
                >
                  詳細
                </a>
              </div> */}
            </div>
          </div>
        </div>
      </section>
      {/* SafeRTOS */}
      <section class="py-5">
        <div class="container px-5 my-5">
          <div class="row gx-5 align-items-center">
            <div class="col-lg-6 order-first order-lg-last">
              <img
                class="img-fluid rounded mb-5 mb-lg-0"
                src="/images/logo-safertos.svg"
                alt="..."
              />
            </div>
            <div class="col-lg-6">
              <h2 class="fw-bolder">
                安全規格の事前認証済み
                <br />
                リアルタイムOS
              </h2>
              <p class="lead fw-normal text-muted mb-0">
                セーフティーシステム会社、WITTENSTEIN High Integrity
                Systems社が開発したリアルタイムOS。
              </p>
              {/* <div className="my-4">
                <a
                  href="/product_safertos/"
                  class="btn btn-primary btn-lg"
                  aria-disabled="true"
                >
                  詳細
                </a>
              </div> */}
            </div>
          </div>
        </div>
      </section>
      {/* TraceAlyzer */}
      <section class="py-5 bg-light" id="scroll-target">
        <div class="container px-5 my-5">
          <div class="row gx-5 align-items-center">
            <div class="col-lg-6">
              <img
                class="img-fluid rounded mb-5 mb-lg-0"
                src="/images/logo-tracealyzer.svg"
                alt="..."
              />
            </div>
            <div class="col-lg-6">
              <h2 class="fw-bolder">
                TraceAlyzer
                <br />
              </h2>
              <p class="lead fw-normal text-muted mb-0">
                リアルタイム可視化ツール
              </p>
            </div>
          </div>
        </div>
      </section>
      {/* CycloneTCP */}
      <section class="py-5">
        <div class="container px-5 my-5">
          <div class="row gx-5 align-items-center">
            <div class="col-lg-6 order-first order-lg-last">
              <img
                class="img-fluid rounded mb-5 mb-lg-0"
                src="/images/logo-cyclone-tcp.svg"
                alt="..."
              />
            </div>
            <div class="col-lg-6">
              <h2 class="fw-bolder">
                組み込み専用
                <br />
                IPv4/IPv6デュアルスタック
              </h2>
              <p class="lead fw-normal text-muted mb-0"></p>
            </div>
          </div>
        </div>
      </section>
      {/* Testimonial section*/}
      <div className="py-5 bg-light">
        <div className="container px-5 my-5">
          <div className="row justify-content-center">
            <div className="col-lg-10">
              <div className="text-center">
                <div className="fs-4 mb-4 fst-italic">
                  “Dioiz has been in the embedded engineering industry more than a decade.<br/>
                  With rich low and high level technical support experience,<br/>
                  Dioiz can advise safe path to achieve your embedded project."<br/>
                </div>
                <div className="d-flex align-items-center justify-content-center">
                  <div className="fw-bold">
                    <span className="fw-bold text-primary mx-1"></span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  </Layout>
)

export default IndexPage
